<template>
    <div class="layout">
        <Layout>
            <my-head @back="back"></my-head>
            <Content :style="{padding: '0 50px'}">
                <Row type="flex" justify="center" align="middle" class="code-row-bg">
                    <Col span="22" push="2" type="flex" justify="end">
                    <Row type="flex" justify="end" class="code-row-bg">
                        <DatePicker :editable="false" @on-change="setime" format="yyyy-MM-dd" type="daterange"
                                    lacement="bottom-end"
                                    placeholder="Select date"
                                    style="width: 180px"></DatePicker>
                        <Select v-model="model1" style="width:100px;margin: 0 20px;">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}
                            </Option>
                        </Select>
                        <Input v-model="SearchCon" placeholder="Enter Content..." style="width: 220px"></Input>
                        <Button @click="search" type="info" icon="ios-search" style="margin: 0 20px">Search</Button>
                        <Checkbox @on-change="Checkbox" label="twitter" style="padding-top: 7px;">
                            <span>只看待收货</span>
                        </Checkbox>
                    </Row>
                    </Col>
                    <Col span="2" pull="22">
                    <Breadcrumb :style="{margin: '20px 0'}">
                        <BreadcrumbItem>收货单</BreadcrumbItem>
                    </Breadcrumb>
                    </Col>
                </Row>
                <Card style="padding: 0">
                    <div>
                        <Table @on-row-click="push" :loading="loading" border stripe :columns="columns1" :data="data1"
                               style="height: 520px;"></Table>
                    </div>
                    <Row type="flex" justify="end" class="code-row-bg">
                        <Col style="margin: 18px 15px 0  0;">
                        共<span style="color: #D72236;font-size: 18px;font-weight: 700"> {{total}} </span>条数据，有<span
                            style="color: #D72236;font-size: 18px;font-weight: 700"> {{Untreated}} </span>条待处理订单
                        </Col>
                        <Page @on-change="pageCli" :total="total" :page-size="10" :current="cuent"
                              style="margin-top: 16px;"></Page>
                    </Row>
                </Card>
            </Content>
            <Footer class="layout-footer-center" style="text-align: center">2011-2018 &copy; 怡乐仓库</Footer>
        </Layout>
    </div>
</template>
<script>
    export default {
        name: 'Take',
        data () {
            return {
                user: {},
                cuent: 1,
                loading: true,
                total: 1,
                time: '',
                model1: '单据号',//下拉框值
                associate_no: '',//采购单
                publisher_no: '',//供应商
                SearchCon: '',
                Check: '',
                Untreated: '',
                cityList: [
                    {
                        value: '单据号',
                        label: '单据号'
                    },
                    {
                        value: '供应商',
                        label: '供应商'
                    }
                ],
                columns1: [
                    {
                        title: '单据号',
                        key: 'associate_no'
                    },
                    {
                        title: '供应商',
                        key: 'publisher_no'
                    },
                    {
                        title: '制单时间',
                        key: 'dt',
                        render: function (h, a) {
                            return a.row.dt.split('T')[0]
                        }
                    },
                    {
                        title: '制单人',
                        key: 'chn_name'
                    },
                    {
                        title: '操作',
                        key: 'pz_nums',
                        render: function (h, a) {
                            if (a.row.status == 1) {
                                return <p style="color:#ed3f14">收货</p>
                            } else {
                                return '留档管理'
                            }
                        }
                    },
                    {
                        title: '状态',
                        render: function (h, a) {
//                            return a.row.status
                            if (a.row.status == 1) {
                                return '待收货'
                            } else {
                                return <p class="ivu-icon ivu-icon-checkmark"
                                          style="cursor: pointer;font-size:14px"></p>
                            }
                        }
                    }
                ],
                data1: []
            }
        },
        methods: {
            //采购单列表
            Stock(num){
                var v = this;
                v.cuent = num
                v.loading = true;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/GetInStockListpage', v.Qs.stringify({
                    bookstore_name: v.user.bookstore_name,
                    status: v.Check,
                    pagenum: num,
                    pagesize: 10,
                    associate_no: v.associate_no,
                    publisher_no: v.publisher_no,
                    startdate: v.time[0],//开始时间
                    enddate: v.time[1],//结束时间
                })).then(function (res) {
                    if (res.data.error == 0) {
                        v.data1 = res.data.data;
                        v.total = res.data.total;
                        v.Untreated = res.data.Untreated;
                    } else {
                        v.data1 = [];
                        v.total = 1;
                        v.Untreated = '';
                    }
                    v.loading = false;
                }).catch(function () {
                    v.$Message.error('对方不想说话，并且向你抛出了一个异常');
                })
            },

            //列表点击跳转
            push(i, h){
                this.$router.push('/TakeVou?associate_n=' + i.associate_no + '')
            },

            //只看待收货
            Checkbox(i){
                i == true ? this.Check = 1 : this.Check = '';
                this.Stock(1);
            },

            //分页回调
            pageCli(i){
                this.Stock(i)
            },

            back(i){
                this.user = i
            },

            search(){
                var v = this;
                if (v.model1 == '单据号') {
                    v.associate_no = v.SearchCon;
                    v.publisher_no = ''
                } else {
                    v.associate_no = '';
                    v.publisher_no = v.SearchCon;
                }
                v.Stock(1)
            },

            setime(i){
                this.time = i
            }
        },
        mounted(){
            this.Stock(1);
        },
        beforeCreate(){
        }
    }

</script>

<!-- Add "scoped" attribute to limit less to this component only -->
<style scoped lang="less">
</style>



